@font-face{
    font-family: outfit;
    src: url(../../public/Outfit-VariableFont_wght.ttf);
}

.InfoSlider{
    width: 100%;
    padding: 5% 10%;
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin: 2% 0%;
    overflow: hidden;
}
.Slide1{
    border-radius: 30px;
    padding: 2% 5%;
    position: relative;
    background-color: #404040bd;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset; 
}
.Slider1Img{
    text-align: center;
}
.Slider1Img>h4,.Slider2Img>h4{
    font-size: 20px;
    color: white;
   letter-spacing: 0.1em;
}
.Slide2{
    position: relative;
    border-radius: 30px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    padding: 2% 5%;
    background-color: #404040bd;
} 
.Slider2Img{
    text-align: center;
}
.Slider1Info , .Slider2Info{
    font-family: outfit;
    text-align: start;
     color: white;

}
@media (max-width:1000px){
    .InfoSlider{
        padding: 0% 2%;
    }
    .Slider1Img>img , .Slider2Img>img{
        width: 100px;
        height: 100px;
    }
    .Slider1Img>h4,.Slider2Img>h4{
        font-size: 18px;
    } 
    .Slider1Info , .Slider2Info{
        font-size: 14px;
        font-family: outfit;
        text-align: start;
        color: white;
    }
    
}
@media (max-width:600px){
    .Slider1Info , .Slider2Info{
        font-size: 13px;

    }

}
