@font-face{
    font-family: pacifico;
    src: url(../../public/Pacifico-Regular.ttf);
}
@font-face{
    font-family: comfortaa;
    src: url(../../public/Comfortaa-Regular.ttf);
}

.InfoSlider{
    text-align: center;
    width: 100%;
    padding: 0% 8%;
    
}
.Sw{
    width: 100%;
}
.InfoBox{
    text-align: center;
    margin: 5px auto;
    border-radius: 50% 50% 0% 0%;
    text-align: center;
    outline: 1.5px solid rgba(0, 0, 0, 0.39);
    width: 200px;
    padding: 10px 0px;
    background-color: rgba(245, 222, 179, 0.366);
}
.swiper-slide{
    text-align: center;
}
.Infobody>img{
    position: relative;
    top: 5px;
    padding: 3px;
    border-radius: 50%;
    outline: 1px solid black ;
    width: 50px;
    height: 50px;
}
.Infobody>h2{
    letter-spacing: 0.1em;
    font-family: pacifico;
}
.Infobody>h2,.Infobody>h4{
    color: var(--orange);
    text-decoration: none;
}
.Infobody>h4{
    font-family: comfortaa;
    font-weight: 800;
}
.Infobody>p{
    width: 100%;
}
@media (max-width:1000px){
    .InfoSlider{
        padding: 5px 5px;
    }
    .InfoBox{
        margin: 0 3%;
        width: 100px;
        margin: 10px;
    }
    .InfoBox{
        width: 150px; 
        padding: 3px 1px;
    }
    .Infobody>img{
        width: 30px;
        height: 30px;
    }
    .Infobody>h2{
        font-size: 25px;
    }
    
}