.header{
    width: 100%;
    background-color: green;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    text-align: center;
    padding: 5px 3% 3px 0px;
    gap: 1.5%;
}
.header>span{
    font-family: sans-serif;
    font-weight: 700;
    margin-right: 1.3%;
}
.header .links{
    font-weight: 300;
}
.links:hover{
    color: white;
}