.contactImage{
    width: 100%;
    height: 50%;
    padding: 1%;
}
.contactImage>img{
    transform: scaleY(0.5);
    transform: scaleX(1);

}
.contactInfo{
    background-color: rgba(128, 128, 128, 0.459);
    display: flex;
    gap: 4%;
    width: 80%;
    font-family: outfit;
    border-radius: 10px;
    margin: 0 auto;
    justify-content: space-between;
}
.detailsInput{
    width: 50%;
    padding: 2% 5%;
    display: flex;
    gap: 1%;
    text-align: center;
    flex-direction: column;
}
.detailsInput>input,.detailsInput>textarea{
    padding: 2% 4%;
    border-radius: 10px;
    outline: none;
    border: none;
    margin: 2% auto;
}
.detailsInput>textarea{
    width: 100%;
}
.detailsInput>input:focus{
    outline: none;
}
.detailsInput>button{
    width: 50%;
    margin: 2% auto;
    padding: 2% 5%;
    font-size: 18px;
    font-weight: 500;
    text-transform: uppercase;
    background-color: var(--orange);
    border: none;
    border-radius: 30px;
    cursor: pointer;
}
.detailsAddress{
    padding: 2% 3%;
    border-left: 1px solid black ;
}
.detailsInput>h3{
    color: var(--orange);
}
.Ad>h4,.Ph>h4,.Em>h4{
    color: var(--orange);
}
.googleMaps{
    width: 60%;
    margin: 2% auto;
    height: 500px;
}
@media (max-width:1000px){
    .contactInfo{
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .detailsInput{
        margin: 0 auto;
        width: 80%;
    }
    .detailsInput>input{
        margin: 2%;
        width: 80%;
        border: none;
        padding: 5px 10px;
        border-radius: 4px;
    }
    .contactImage>img{
        transform: scaleY(1);
        transform: scaleX(1);
    }
    .detailsAddress{
        font-size: 14px;
    }
    .contactInfo{
        border-radius: 0px;
    }
    .googleMaps{
        width: 90%;
        margin: 2% auto;
        height: 300px;
    }
}