.ImageBox{
    width:100%;
    height: 100%;
    display: flex;
    padding: 1% 10%;
    text-align: center;
    justify-content: center;
    margin: 0 auto;
    overflow: hidden;
}
.slide{

    display: flex;
    width: 90%;
    height: 100%;
    margin: 0 auto;
    margin-top: 5%;
    overflow: hidden;
    position: relative;
    padding: 0px;
    box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px;
}
.leftArrow{
    position: absolute;
    top: 50%;
    z-index: 999;
    font-size: 2em;
    z-index: 0;
    cursor: pointer;
}
.rightArrow{
    position: absolute;
    top: 50%;
    right: 0;
    z-index: 999;
    font-size: 2em;
    cursor: pointer;
}
.content
{
    width: 100%;
    height: 50%;
    z-index: -1;
    margin: 0 auto;
    opacity: 0;
    transform: translateX(-50%);
    transition: all 3s;
    animation: slideRight 1s ease 0s;
    animation-fill-mode: forwards;
}
.Image{
    width: 100%;
    height: 500px;
}
@keyframes slideRight {
    100%{
        opacity: 1;
    transform: translateX(0%);
    transition: all 1s;
        
    }
    
}
.showContent{
    opacity: 1;
    transform: translateX(0%);
    transition: all 3s;
}
.content>p{
    box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px;
    background-color: rgba(99, 97, 97, 0.616);
    border-radius: 5px;
    color: rgb(255, 255, 255);
    position: absolute;
    font-size: 1.1em;
    font-family:outfit;
    padding: 2% 4%;
    width: 70%;
    text-align:center;
    font-weight: 300;
    height: fit-content;
    /* overflow-wrap:break-word;
    word-break: break-all; */
    margin:0% 15%;
    animation: slide-up 1s ease 1s;
    /* visibility: hidden; */
    animation-fill-mode:forwards;
}
.youtubeContent{
    width: 100%;
    height: 40vh;
    background-color:var(--orange);
    padding: 1em 0em;
}
.Player{
    width: 40%;
    height: 100%;
    margin: 0% auto;
    
}
.founderInfo{
    font-family: outfit;
    width: 80%;
    display: flex;
    margin: 0 auto;
    padding: 2%;
    justify-content: center;
    gap: 3%;
}
.info{
   width: 70%;  
}
.infoImg{
    width: 30%;
}
.infoImg>img{
    margin-top: 5%;
    padding: 2% 0%;
    border-radius: 50%;
}
.info>h3{
    font-size: 1.5em;
    color: var(--orange);
}
.info>p{
    font-size: 18px;
}
@font-face{
    font-family: outfit;
    src: url(../../public/Outfit-VariableFont_wght.ttf);
  }
.info>p>span{
    font-size: 20px;
    font-family: outfit;
    font-weight: bolder;
    font-style: oblique;
}
.info>a{
    padding: 1% 3%;
    border: none;
    background-color: var(--green);
    border-radius: 15px;
    font-weight: 600;
}
@media (max-width:1200px) {


    .slide
    {
        width: 90%;
        height: 50%;
    }

}
@media (max-width:1000px) {
    .ImageBox{
        padding: 3px 5px;
    }
    .content>p{
        font-family: monospace;

    }
    .Image{
        width: 100%;
        height: 200px;
    }
    .info>a{
        padding: 10px 20px;
    }
    .founderInfo{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
    }
    .info>p{
        text-align: start;
        font-size: 14px;
        padding: 0px 10px;
        font-family: outfit;
    }
    .info{
        margin: 0 auto;
    }
    
    .infoImg{
        padding: 5%;
        margin: 0 auto;
        width: 60%;
    }
    .infoImg>img{
        border-radius: 0%;
    }

    .Player{
        width: 80%;
        height: 100%;
        margin: 0% auto;
        
    }

    .slide
    {
        width: 90%;
        height: 50%;
    }
        
    }
@keyframes slide-up {
    0%{
        visibility: hidden;
        bottom: -50%;
    }
    
    100%{
        visibility: visible;
        bottom: 5%;  
    }
}

