@font-face{
    font-family: auro;
    src: url(../../public/Arvo-Regular.ttf);
}

@font-face{
    font-family: carterOne;
    src: url(../../public/CarterOne-Regular.ttf);
}
.navbar{
    width: 100%;
    display: flex;
    text-align: center;
    justify-content: space-between;
    padding: 3px 3px;
    position: sticky;
    top: 0px;
    z-index: 1000;
    background-color: white;
    box-sizing: border-box;
}
.nav-left{
    padding: 0em 1.6em;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-align: center;
    white-space: nowrap;
    position: relative;
}
.nav-left>img{
    width: 70px;
    height: 70px;
    margin: 2% 0%;
    border-radius: 50%;
}
.nav-left>h1{
    font-family:carterOne;
    margin-left: 1.5%;
    font-size: 40px;
}
.nav-left>h1>span{
    font-size: 0.8em;
}
.nav-right
{
    width: 100%;
    display: flex;
    justify-content: center;
    text-align: center;
}
.nav-right>ul{
    font-family: auro;
    display: flex;
    text-align: center;
    justify-content: space-evenly;
    flex-direction: row;
    padding: 20px;
    width: 70%;
    margin-top: 0px;
    transform: translateX(20%);
    gap: 3%;
}

.nav-right>ul>a{
display: flex;
justify-content: center;
text-align: center;
height: fit-content;
padding: 10px 20px;
border-radius: 5px;
font-size: 1em;
}
.nav-right>ul>a>li{
    font-size: 1.05em;
    letter-spacing: 0.05em;
}
.nav-right>ul>a:hover{
    background-color: transparent;
    outline: 1.5px solid var(--orange);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}
.nav-right>ul>a:nth-child(6)
{
    background-color: var(--orange);
    border-radius: 15px;
    font-weight: 700;
}
.menuIcon{
 display:none;
 position: absolute;
 bottom: 20%;
 text-align: center;
 font-size: 1.3em;
 right: 2%;
}
.menuActive{
    outline: 1.5px solid var(--orange);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}
/*  Navbar Design*/
@media (max-width:1480px) {
    .nav-right>ul{
        transform: translateX(0%);
    }
    
}
@media (max-width:1200px) {
    .nav-right>ul{
        transform: translateX(0%);
    }
    
}
/*  CSS Mobile Design     */
@media (max-width:500px){
    .nav-left>h1{
        font-size: 0.8em;
    }
    .nav-left>h1>span{
        font-size: 0.7em;
    }
    .navbar{
        position: sticky;
        top: 0px;
    }
    .nav-left{
        padding: 0em 0.5em;
    }
    
}
@media (max-width:1000px) {
    .navbar{
        position: sticky;
        top: 0px;
    }
    .menuIcon{
        cursor: pointer;
        display: block;
    }
    .navbar{
        display: block;
        flex-direction: column;
    }
     .nav-right>ul{
        background-color: rgba(255, 255, 255, 0.89);
        position: absolute;
        top: 0;
        width: 40%;
        left: 0;
        z-index: 30;
        display: flex; 
        box-shadow: rgb(38, 57, 77) 5px 20px 30px -10px;
        transform: translateX(0%);
        flex-direction: column;
        justify-content: center;
        text-align: center;
     }
     .nav-right>ul>a{
        margin-top: 10%;
     }
     .nav-right>ul>a>li{
        text-align: center;
     }
     .nav-left{
        justify-content: flex-start;
     }
     .nav-left>h1{
        margin-left: 1%;
        font-size: 1.6em;
    }
    #hide
    { transition: left 1.2s linear;
        left: -100%;
    }
    #show{
        transition: left 0.8s linear;
       left: 0%;
    }
    .nav-left>img{
        width: 50px;
        height: 50px;
        border-radius: 50%;
    }
    
    
}
