.footer{
    background-color: green;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    box-sizing: border-box;
    width: 100%;
    padding: 2% 4%;
    margin-top: 10px;
    gap: 5%;
    display: flex;
    justify-content: space-evenly;

}
h2{
    text-decoration: underline;
}
.footer-left{
    width: 50%;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.helps-section{
    text-align: start;
}
.footer-right{
    color: white;
}
.address>h4>span{
    color: var(--orange);
    font-weight: 600;
}
.closer{
    padding: 2% 0%;
    text-align: center;
    background-color: rgb(105, 104, 104);
}
.links{
    display: flex;
    flex-direction: row;
    gap: 2%;
    justify-content: center;
    padding: 2% 0%;
    font-size: 20px;
    color: white;
}
.links>span{
    font-weight: 500;
}
.links>a{
    color: white;
}
.links>a:hover{
  color: green;
}
.donation>a>button{
    border: none;
    cursor: pointer;
    font-size: 20px;
    padding: 2% 5%;
    border-radius: 40px;
    color: white;
    font-weight: 600;
    background-color: var(--orange);
}
.donation>h5{
    color: white;
    font-size: 12px;
    margin-top: 1.5%;
}
.copyRight>h4,.copyRight>h5{
    color: white;
}
@media (max-width:1000px){
    .footer{
        display: flex;
        flex-direction: column;
    }
    .footer-left{
        font-size: 15px;
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .footer-right{
        display: block;
        width: 100%;
        font-size: 15px;
    }
    .helps-section>ul>h2{
        white-space: nowrap;
      flex-wrap: nowrap;
    }
    .helps-section>ul{
        padding: 0% ;
    }
    .helps-section{
        width: 60%;
        padding: 0%;
    } 

}
