.ServiceHeader{
    width: 95%;
    margin: 0% auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 3%;
    padding: 0% 2% 0% 0%;
    background-color: rgba(128, 128, 128, 0.781);
  
}
.serviceImg>img{
    width: 500px;
    height: 500px;
}
.serviceInfo{
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-self: center;
    width: 100%;
    height: 100%;
    margin: auto 0px;
    border-radius: 15px;
    font-family: outfit;
    color: white;
    padding: 0px 2%;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    background-color: rgba(128, 128, 128, 0.781);
  

}
.serviceInfo>h1{
    text-decoration: underline;
    text-align: center;
}
.serviceBody1,.serviceBody2{
    background-color: rgba(128, 128, 128, 0.719);
    width: 80%;
    margin: 10px auto;
    color: white;
    padding: 1% 2%;
    text-align: center;
}
.serviceBody1>h1{
    color: white;
}
.serviceInfos{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
     font-family: outfit;
}
.serInfo{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    border-radius: 10px;
    background-color: gray;
    padding: 3%;
}
.serInfo1{
    padding: 5px 5%;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    border-radius: 10px;
    background-color: gray;
}
.serInfo>h1,.serInfo1>h1{
    color: var(--orange);
}
.serviceInfos1
{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    font-family: outfit;
    background-color: gray;
    border-radius: 10px;
    padding: 10px 15px;
}
.serviceBody2{
    text-align: left;
}
.serviceBody2>h1{
    text-align: center;
}
@media (max-width:1000px)
{
    .ServiceHeader{
        width: 99%;
        padding: 3px 0px;
        flex-direction: column;
    }
    .serviceBody1{
        width: 98%;
    }
    .serviceImg>img{
        padding: 3px;
        border-radius: 10px;
        width: 100%;
        height: 300px;
    }
    .serviceBody2{
        width: 98%;
        padding: 10px 20px;
    }
    
    .serviceInfos1{
        padding: 10px 20px;
    }
}
@media (max-width:800px){
    .serviceInfos{
        flex-direction: column;
    }
    .serInfo{
        background-color: rgba(255, 255, 255, 0.647);
        padding: 10px;
        margin: 10px;
    }
    .serInfo1{
        background-color: rgba(255, 255, 255, 0.647);
        margin: 15px;
    }
    .serInfo1>h4,.serInfo>h4{
        color: gray;
    }
   

}
