.aboutImage{
    width: 100%;
    display: block;
}
.AboutInfo{
    width: 80%;
    margin: 0 auto;
    background-color: rgba(128, 128, 128, 0.271);
    border-radius: 10px;
    padding: 3% 5%;
}
.AboutInfo>h4{
    color: var(--orange);
    text-decoration: underline;
    font-size: 19px;
}
.AboutInfo>p{
    font-size: 19px;
    font-family: outfit;
}
.AboutInfo>p>span{
    color: var(--orange);
    font-weight: 600;
}
.AboutFounder{
    padding: 2% 5%;
    width: 80%;
    display: flex;
    margin:  2% auto;
    font-family: outfit;
    border-radius: 10px;
    background-color:  rgba(128, 128, 128, 0.271);
}

.founderInfo{
    display: block;
}
.founderInfo>h3{
    color: var(--orange);
    text-decoration: underline;
}
.info{
    width: 100%;
}
.logo{
    display: flex;
    justify-content: center;
    text-align: center;
    margin: 5% 0%;
    width: 80%;
    height: 80%;
}
.AboutStarted,.AboutShelter{
    font-family: outfit;
    text-align: start;
    width: 80%;
    margin: 5% auto;
    background-color:rgba(128, 128, 128, 0.271);
    border-radius: 10px;
    padding: 3% 5%;
}
.AboutStarted>h4,.AboutShelter>h4{
    color: var(--orange);
    text-decoration: underline;
}
.AboutShelter>a{
    margin: 0% auto;
}
.galleryLink{
    text-align: center;
    margin: 0 auto;
    padding: 10px 20px;
}
.galleryLink>a{
    background-color: var(--orange);
    padding: 5px 10px;
    border-radius: 5px;
    font-weight: bolder;
}

@media (max-width:1000px){
    .AboutInfo>p{
        font-size: 16px;
    }
    .AboutInfo{
        padding: 10px 20px;
        font-size: 15px;
        width: 95%;
    }
    .info>p{
        font-size: 16px;
    }
    .AboutFounder{
        padding: 10px 20px;
        font-size: 16px;
        width: 95%;
    }
    .AboutStarted{
        padding: 10px 20px;
        font-size: 16px;
        width: 95%;
    }
    .AboutShelter{
        padding: 10px 20px;
        font-size: 15px;
        width: 95%;
    }
    .logo{
        display: none;
    }
}