.GSlider{
    width: 80%;
    margin: 0 auto;
    display: flex;
    gap: 5%;
    justify-content: center;
    flex-wrap: wrap;
}
.ImgSlider{
    width: 500px;
    height: 500px;
    box-shadow: rgb(38, 57, 77) 5px 20px 30px -10px;
    background-color: black;
    text-align: center;
    margin: 1%;
}
@media (max-width:1000px){
    .ImgSlider{
        margin: 10px 0px;
        border-radius: 2px;
        height: 300px;
    }
    .ImgSlider>img{
        border-radius: 2px;
    }
}